import { NextSeo } from 'next-seo';
import React, { useContext, useState } from 'react';
import { Button, Card, CardActions, CardContent, Dialog, DialogContent, DialogTitle, Divider, Stack, Typography } from '@mui/material';
import { AppContext } from '@components/common/context/AppProvider';
import { cleanUnderscore, prefix } from 'utility/helpers';
import { CardTitleAndValue, CenteredStack, TitleAndValue } from '@components/common/styles';
const trashDisclaimer = 'Estimate disclaimer: Randomly generated data is included in calculation, introducing variability. Figures are approximation, not precise value.';
const Islands = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    islands
  } = state?.account || {};
  const [dialog, setDialog] = useState({
    open: false,
    data: null
  });
  // const highestTotalStats = useMemo(() => getHighestCharacterByTotalStats(state?.characters), [state?.characters]);
  const handleLearnMoreClick = island => {
    setDialog({
      data: island,
      open: true
    });
  };
  return <>
    <NextSeo title="Islands | Idleon Toolbox" description="Islands progressions and available upgrades" data-sentry-element="NextSeo" data-sentry-source-file="islands.jsx" />
    <Stack direction={'row'} flexWrap={'wrap'} gap={2} data-sentry-element="Stack" data-sentry-source-file="islands.jsx">
      <CardTitleAndValue title={'Bottles'} value={Math.round(islands?.bottles)} data-sentry-element="CardTitleAndValue" data-sentry-source-file="islands.jsx" />
      <CardTitleAndValue title={'Bottles/day'} value={islands?.bottlesPerDay} data-sentry-element="CardTitleAndValue" data-sentry-source-file="islands.jsx" />
      <CardTitleAndValue title={'Trash/day *'} value={islands?.trashPerDay} tooltipTitle={trashDisclaimer} data-sentry-element="CardTitleAndValue" data-sentry-source-file="islands.jsx" />
      <CardTitleAndValue title={'Unclaimed days'} value={islands?.numberOfDaysAfk || '0'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="islands.jsx" />
    </Stack>
    <Stack direction={'row'} gap={2} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="islands.jsx">
      {islands?.list?.map((island, index) => {
        const {
          name,
          description,
          unlocked,
          cost,
          trash,
          currentTrial,
          bestDpsEver,
          shimmerCurrency,
          hoursAfk,
          learnMore
        } = island;
        return <Card key={name} sx={{
          width: 300,
          opacity: unlocked ? 1 : .5,
          display: 'flex',
          flexDirection: 'column'
        }}>
          <CardContent>
            <Stack gap={2}>
              <Stack gap={2} direction={'row'} alignItems={'center'}>
                <img src={`${prefix}data/IslandSail${index}.png`} alt={''} />
                <Typography>{name}</Typography>
              </Stack>
              <Typography>{cleanUnderscore(description)}</Typography>
              <Divider />
              {!unlocked ? <Typography>Cost: {cost}</Typography> : null}
              {unlocked && island.hasOwnProperty('trash') ? <CenteredStack>
                <img src={`${prefix}etc/Trash_Currency.png`} alt={''} />
                <Typography>{trash.toFixed(2)}</Typography>
              </CenteredStack> : null}
              {unlocked && currentTrial ? <Stack gap={1}>
                <TitleAndValue title={'Current trial'} value={cleanUnderscore(currentTrial)} />
                <TitleAndValue title={'Best DPS ever'} value={cleanUnderscore(bestDpsEver)} />
                <CenteredStack>
                  <img src={`${prefix}etc/Shimmer_Currency.png`} alt={''} />
                  <Typography>{shimmerCurrency}</Typography>
                </CenteredStack>
              </Stack> : null}
              {unlocked && hoursAfk ? <Stack gap={1}>
                <TitleAndValue title={'Hours Afk'} value={cleanUnderscore(hoursAfk)} />
              </Stack> : null}
            </Stack>
          </CardContent>
          <CardActions sx={{
            mt: 'auto'
          }}>
            {learnMore ? <Button onClick={() => handleLearnMoreClick(island)} size="small">Learn More</Button> : null}
          </CardActions>
        </Card>;
      })}
    </Stack>
    <Dialog open={dialog.open} onClose={() => setDialog({
      ...dialog,
      open: false
    })} data-sentry-element="Dialog" data-sentry-source-file="islands.jsx">
      <DialogTitle data-sentry-element="DialogTitle" data-sentry-source-file="islands.jsx">{dialog?.data?.name} shop</DialogTitle>
      <DialogContent data-sentry-element="DialogContent" data-sentry-source-file="islands.jsx">
        <Stack gap={1} data-sentry-element="Stack" data-sentry-source-file="islands.jsx">
          {dialog?.data?.shop?.map(({
            effect,
            cost,
            upgrades,
            unlocked,
            name
          }, index) => {
            const isImage = effect.includes('etc') || effect.includes('data');
            return <Card variant={'outlined'} key={'effect-' + index} sx={{
              border: unlocked ? '1px solid' : '',
              borderColor: unlocked ? 'success.light' : ''
            }}>
              <CardContent>
                {name ? <Typography>{name}</Typography> : null}
                <Stack gap={isImage ? 1 : 0} direction={isImage ? 'row' : 'column'} alignItems={isImage ? 'center' : 'flex-start'}>
                  {isImage ? <img style={{
                    width: 30
                  }} src={`${prefix}${effect}.png`} alt={''} /> : <Typography>{cleanUnderscore(effect)}</Typography>}
                  <Typography>Cost: {cost} {upgrades ? `(${upgrades})` : ''}</Typography>
                </Stack>
              </CardContent>
            </Card>;
          })}

        </Stack> </DialogContent>
    </Dialog>
  </>;
};
export default Islands;